<template>
  <div class="qyxx-page level">
    <headerBar :header-data="headerData" />
    <div class="container container-page">
      <div class="content">
        <div class="mine-list negative box">

          <!--  <van-cell class="cell-list" title="公司LOGO">
            <van-icon slot="right-icon"><em class="photo" /> </van-icon>
          </van-cell> -->

          <div class="page-list" style="align-items: center;">
            <div class="pagelist-left">个人头像</div>
            <div class="pagelist-right"><em class="photo-tx"> <van-image fit="cover" :src="imageList.img1" /> </em> </div>
          </div>

          <van-cell class="cell-list" title="公司名称" :value="userInfo.zzmc" />
        </div>
      </div>

      <div class="content">
        <div class="mine-list box">
          <van-cell class="cell-list" title="姓名" :value="xm" />
          <van-cell class="cell-list" title="性别" :value="XB[xb]" />
          <van-cell class="cell-list" title="固定电话" :value="gddh" />
          <van-cell class="cell-list" title="手机" :value="sj" />
          <van-cell class="cell-list" title="电子邮箱" :value="dzyj" />
        </div>
      </div>

      <!-- 退出按钮 begin -->
      <div v-if="!ddLogin" class="loginout-button">
        <van-button type="default" @click="outLogin()">退出登录</van-button>
      </div>
      <!-- 退出按钮 begin -->
    </div>
    <!-- </v-touch> -->
  </div>
</template>

<script land="script" scoped>
import { mapMutations, mapState } from 'vuex'
import { getToken } from '@/common/js/catch'
import constants from '../../../../common/js/constants'
import headerBar from '@/components/header/headerBar.vue'
import { pageRouter } from '@/common/js/mixin.js'
import { getUserInfo } from '@/store/module.js'
export default {
  name: 'GrzxGrxx',
  components: {
    headerBar
  },
  mixins: [pageRouter],
  data() {
    return {
      qyxxList: [],
      cz: '',
      xm: '',
      xb: '',
      gddh: '',
      sj: '',
      dzyj: '',
      headerData: {
        title: '个人信息',
        leftUrl: 'back',
        leftText: ' '
      },
      imageList: {
        img1: require('@/assets/images/tx.png')
      }
    }
  },
  computed: {
    // 取数据
    ...mapState({
      isLogin: state => state.user.isLogin,
      ddLogin: state => state.dd.ddType,
      userInfo: state => state.user.userInfo
    })
  },
  /* 添加钉钉判断 */
  created() {
    this.XB = constants.XB
    this.getQyxx()
  },
  methods: {
    ...mapMutations([
      'SET_IS_LOGIN'
    ]),
    onSwipeRight() {
      this.$router.go(-1)
    },
    getQyxx() {
      getUserInfo.url = '/app/zzqx/gg_ry/getGg_ryByRyid/' + this.userInfo.id + '?token=' + getToken()
      this.asyncApiModule(getUserInfo).then(res => {
        // if (res.length === 0) {
        //   this.noNotice = true
        // }
        // this.$refs.scrollContent.loadData()
        this.loading = false
        if (res.length === 0) {
          this.nomore = true
        } else {
          this.qyxxList = res
          const respon = JSON.stringify(res)
          const response = JSON.parse(respon)

          this.cz = response.cz
          this.xm = response.xm

          this.xb = response.xb

          this.gddh = response.gddh
          this.sj = response.sj
          this.dzyj = response.dzyj
          // cz: '',
          //       xm: '',
          //       xb: '',
          //       gddh: '',
          //       sj: '',
          //       dzyj: ''

          // this.$refs.scrollContent.refresh()
        }
        // this.$router.push('/zc')
      // eslint-disable-next-line no-unused-vars
      })
    },

    outLogin() {
      // Dialog.failed({
      //   title: '提示',
      //   content: '是否确定退出登录',
      //   cancelText: '取消',
      //   confirmText: '确定',
      //   onConfirm: () => {
      //     this.SET_IS_LOGIN(false)
      //     this.$router.push('/jbrlogin')
      //   }
      // })
    },
    testlogin() {
      this.$router.push('/home')
    }
  }
}

</script>

